*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff"); }

.main-page {
  height: 100%;
  scroll-behavior: smooth; }

.page-body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #1b2d37; }

.page-body .main {
  flex-grow: 1; }

.header {
  position: absolute;
  max-width: 1431px;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 33px 10px 0;
  display: flex;
  justify-content: space-between;
  background-color: transparent; }
  @media (max-width: 1023px) {
    .header {
      padding: 20px 25px; } }
  @media (max-width: 767px) {
    .header {
      padding: 17px; } }

.header__logo-mobile {
  display: none; }
  @media (max-width: 767px) {
    .header__logo-mobile {
      display: block; } }

.header__logo-desktop {
  color: #ffffff; }
  @media (max-width: 767px) {
    .header__logo-desktop {
      display: none; } }

.header__contacts {
  display: flex; }

.header__contacts-list {
  margin: 0 45px;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: -webkit-max-content -webkit-max-content;
  grid-template-columns: max-content max-content;
  -moz-column-gap: 78px;
       column-gap: 78px;
  row-gap: 10px; }
  @media (max-width: 1023px) {
    .header__contacts-list {
      margin: 0;
      -moz-column-gap: 34px;
           column-gap: 34px; } }
  @media (max-width: 767px) {
    .header__contacts-list {
      -moz-column-gap: 20px;
           column-gap: 20px; } }

.header__contacts-list li {
  align-self: center;
  padding: 5px; }
  @media (max-width: 1023px) {
    .header__contacts-list li {
      padding: 0; } }

.header__contacts-list a {
  position: relative;
  text-decoration: none;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  display: flex;
  align-items: center;
  padding: 5px 0; }
  .header__contacts-list a:hover, .header__contacts-list a:focus {
    opacity: 0.5; }
  .header__contacts-list a:active {
    opacity: 1; }

@media (max-width: 1023px) {
  .header__email,
  .header__tel {
    display: none; } }

.header__logo-letter {
  margin: 0 20px 0 0; }
  @media (max-width: 1023px) {
    .header__logo-letter {
      width: 25px;
      height: 19px;
      margin: 0; } }

.header__logo-phone-call {
  margin: 0 14px 0 0; }
  @media (max-width: 1023px) {
    .header__logo-phone-call {
      width: 24px;
      height: 24px;
      margin: 0; } }

.header__button-order {
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  color: #ffffff;
  min-height: 45px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  align-self: center;
  padding: 0 29px 0 24px;
  margin: 0 0 auto 0; }
  .header__button-order:focus {
    outline-color: #c4c4c4; }
  .header__button-order:hover {
    transform: scale(1.05); }
  .header__button-order:active {
    transform: scale(1); }
  @media (max-width: 1023px) {
    .header__button-order {
      display: none; } }

.main__features-background {
  background-image: url("../img/backgrounds/background-about.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #ffffff; }
